.Header {
    background-color: rgb(255, 236, 113);
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wr {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.Buttons {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    flex-wrap: wrap;
    /* overflow: hidden; */
}

.Button {
    border: 1px solid #333;
    background-color: transparent;
    border-radius: 5px;
    padding: 0.25rem 1rem;
}

.Link {
    text-decoration: none;
    color: #333;
}

.Logo img {
    height: 50px;
}

